.load-ani {
  justify-content: center;
  gap: 16px;
  margin-top: 40px;
  display: flex;
}

.load-ani-item {
  height: 16px;
  width: 16px;
  background-color: #fff;
  border-radius: 50%;
}

.load-ani-item:first-child {
  animation: 12s 3s infinite load;
}

.load-ani-item:nth-last-child(2) {
  animation: 12s 6s infinite load;
}

.load-ani-item:last-child {
  animation: 12s 9s infinite load;
}

@keyframes load {
  0% {
    width: 16px;
    border-radius: 50%;
  }

  25% {
    width: 40px;
    border-radius: 40px;
  }

  50% {
    width: 16px;
    border-radius: 50%;
  }

  100% {
    width: 16px;
    border-radius: 50%;
  }
}

