section {
    padding-top: 40px;
        padding-bottom: 40px;
    @include desktop{
    //    padding-left: 40px;
    //    padding-right: 40px;
    }
}

img {
    display: block;
}
textarea {
    resize: none;
    outline: none;
}
input {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    outline: 0;
    border: none;
}
.container {
    width: 100%;
    padding-left: 15px;
    padding-right: 15px;
    margin: 0 auto;
    
    @include mobile {
        width: 320px;
    }
    @include tablet {
        width: 768px;
    }
    @include desktop {
        width: 1280px;
    }
}

// ---------------для секціїї-------------
.takeorder .container {
    background-image: url('../images-accessories/line-1-mob.png'),
    url('../images-accessories/line-2-mob.png');
    background-repeat: no-repeat;
    background-size: contain, contain, cover;
    background-position: 0px 15px, 0px 12px, 0px 0px;
    
    @include tablet {
        background-image: url('../images-takeorder/line-1.png'),
        url('../images-takeorder/line-2.png');
        background-position: 0px 50px, 0px 57px, 0px 0px;
    }
    @include desktop {
        background-image: url('../images-accessories/line-1.png'),
        url('../images-accessories/line-2.png');
        background-position: 0px 60px, 0px 70px, 0px 0px;
    }
}
// -------------головний заголовок

.takeorder-title {
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 600;
    color: var(--primary-text-color);
    @include min-mobile{
      font-size: 26px;
      line-height: 1.16;
      margin-bottom: 35px;
    }
    @include tablet{
        padding-left: 59px;
      font-size: 56px;
      line-height: 1.18;
      margin-bottom: 51px;
    }
    @include desktop{
       font-size: 56px;
       line-height: 1.18;
       padding-left: 142px;
       margin-bottom: 75px;
    }
}
// ------------заголовок який на дисктопі
.takeorder-subtitle {
@include desktop{
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 600;
    font-size: 56px;
    line-height: 1.18;
    color: var(--primary-text-color);
    margin-bottom: 32px;
}
    @media screen and (max-width:1279px) {
        display: none;
    }
}
// ----------заголовок який видно на мобілці
.takeorder-sub-subtitle {
    font-weight: 600;
    font-size: 56px;
    line-height: 1.18;
    color: var(--primary-text-color);
    margin-bottom: 24px;
    @media screen and (min-width: 320px) and (max-width: 469px) {
        font-size: 24px;
    }
    @include mobile {
        font-size: 32px;
    }
    @include tablet {
        font-size: 56px;
    }
    @include desktop {
        display: none;
    }
}
// -----------заголовок перед формой
.takeorder-title-top {
   font-weight: 600;
   color: var(--accent-color);
    @include min-mobile{
            font-size: 24px;
            line-height: 1.17;
            margin-top: 32px;
            margin-bottom: 16px;
    }
    @include tablet{
        font-size: 36px;
            line-height: 1.17;
            margin-top: 48px;
            margin-bottom: 48px;
    }
    @include desktop{
        font-size: 36px;
        line-height: 1.17;
        margin-top: 48px;
        margin-bottom: 48px;
    }
}

.takeorder-title-des {
    display: none;
    @include desktop {
        display: block;
        font-size: 56px;
        color: var(--primary-text-color);
    }
}
.takeorder-picture{
    border-radius: 12px;
}
.takeorder-top {
    
  @include desktop{
    display: flex;
   
        justify-content: space-between;
        align-items: flex-end;
    
    }
}
.takeorder-wrap {
    @include tablet {
        display: flex;
}
    @include desktop {
    align-items: flex-end;
}
}


.takeorder-price-mob {
    margin-bottom: 16px;
    @include tablet {
        display: none;
    }
}

.takeorder-mob{
    display: none;
    @include only-tablet {
        display: block;
        margin-top: 24px;
    }
}
.takeorder-item {
    @include max-mobile{
        width: 60px;
    }
}
.takeorder-picture {
    border-radius: 5px;
}
.takeorder-list {
        display: flex;
        justify-content: space-between;
        @include only-tablet {
            display: none;
        }
    }
 .takeorder-list-mob{
    @include max-desktop{
        display: flex;
        justify-content: space-between;
    }
    @include desktop{
        display: none;
    }
 }
.take-pic-mob{
    border-radius: 4px;
}

.takeorder-part-right {
    @include only-tablet {
        margin-top: 44px;
        margin-left: 40px;
    }
    @include desktop{
        margin-left: 121px;
    }
}

.takeorder-price {
       
        @include min-mobile {
            display: flex;
            flex-wrap: wrap;
            justify-content: space-between;
            margin-bottom: 16px;
            }
            @include desktop {
            display: flex;
            align-items: center;
            gap: 40px;
            margin-bottom: 40px;
    }
}

.takeorder-price-old-mob{
    font-family: 'Roboto';
        font-style: normal;
        font-weight: 600;
        font-size: 16px;
        text-decoration-line: line-through;
        color: var(--secondary-text-color);
}
.takeorder-price-old{
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 600;
    font-size: 24px;
    text-decoration-line: line-through;
    color: var(--secondary-text-color);
    @include min-mobile{
        display: block;
        margin-top: 10px;
        font-size: 16px;
    }
    @include only-tablet {
        display: block;
        margin-bottom: 56px;
    }
}
.takeorder-price-new-mob{
    font-family: 'Roboto';
        font-style: normal;
        font-weight: 600;
        font-size: 32px;
        text-align: right;
        color: var(--accent-color);
}
.takeorder-price-new{
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 600;
    font-size: 48px;
    color: var(--accent-color);
    @include min-mobile{
        display: block;
        margin-top: 24px;
        font-size: 23px;
    }
}



.takeorder-subtitle {
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 600;
    font-size: 36px;
    line-height: 1.17;
    color: var(--primary-text-color);
}
.takeorder-input {
    display: block;
    width: 100%;
    border-bottom: 1px solid var(--accent-color);
    background-color: transparent;
    font-size: 18px;
    line-height: 1.11;
    color: var(--primary-text-color);
    margin-bottom: 64px;
    @include min-mobile{
        width: 100%;
        font-size: 16px;
        line-height: 1.25;
        margin-bottom: 44px;
    }
}
.takeorder-input::placeholder,
.takeorder-textarea::placeholder {
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    color: var(--primary-text-color);
    line-height: 1.11;
    @include min-mobile {
            font-size: 16px;
            line-height: 1.25;
            margin-bottom: 44px;
        }
}
.takeorder-textarea {
    width: 589px;
    height: 336px;
    background-color: transparent;
    border: 1px solid var(--accent-color);
    border-radius: 0px;
    padding: 10px;
    color: var(--primary-text-color);
    @include min-mobile{
        width:287px ;
        height: 95px;
    }
}
.takeorder-label-text {
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 1.11;
    color: #CDCDCD;
    margin-bottom: 4px;
    @include min-mobile{
        font-size: 16px;
        line-height: 1.25;
    }
}
.takeorder-label {
    
    @include min-mobile{
        display: block;
    }
    @include desktop{
        display: flex;
        flex-direction: column;
    }
}
 .takeorder-form {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
 @include desktop{
    flex-wrap:nowrap;
    align-items: baseline;
 }
}
.takeorder-price-wrap {
    display: flex;
}

.takeorder-price-sub {
    display: none;
    @include only-tablet {
        display: block;
        margin-left: 40px;
        margin-top: 69px;
    }
}

.takeorder-right {
    @include desktop{
            margin-left: 20px;
            height: 360px;
    }
}
select {
    appearance: none;
    outline: 0;
    border: none;
    background-color: transparent;
    width: 100%;
}
.takeorder-blok-select {
    position: relative;
    display: flex;
    width: 590px;
    height: 20px;
    overflow: hidden;
    border-bottom: 1px solid var(--accent-color);
    padding-left: 10px;
    margin-bottom: 64px;
    @include min-mobile{
        width: 288px;
    }
}
.takeorder-blok-select--last {
    @include desktop {
        margin-bottom: 0;
    }
}
.takeorder-select {
    font-family: 'Roboto';
    font-style: normal;
    font-size: 16px;
    line-height: 1.11;
    color: var(--primary-text-color);
    cursor: pointer;
}
.takeorder-blok-select::after {
    content: ' ';
    position: absolute;
    right: 10px;
    transform: translateY(-50%);
    display: block;
    pointer-events: none;
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 15.4px 10px 0 10px;
    border-color: var(--primary-text-color) transparent transparent transparent;
}
.contact {
    @include only-tablet {
        padding: 0 60px;
    }
}
